<template>
	<view>
		<!-- 轮播图 -->
		<u-swiper :list="swiperList" previousMargin="30" nextMargin="30" circular :autoplay="true" radius="5"
			bgColor="#ffffff"></u-swiper>
		<!-- 标签 -->
		<u-tabs :list="tabList" :scrollable="false" lineWidth="40" @click="changeTab"></u-tabs>
		<!-- 标签对应商品 -->
		<view class="goods">
			<u-row>
				<u-col span="6" v-for="item in 12" :key="item">
					<u--image :showLoading="true" src="https://cdn.uviewui.com/uview/album/1.jpg" width="80px"
						height="80px" @click="click"></u--image>
				</u-col>
			</u-row>
		</view>
	</view>
</template>

<script>
	// 请求
	import {
		getHome
	} from '@/network/index'
	export default {
		data() {
			return {
				swiperList: [
					'https://cdn.uviewui.com/uview/swiper/swiper3.png',
					'https://cdn.uviewui.com/uview/swiper/swiper2.png',
					'https://cdn.uviewui.com/uview/swiper/swiper1.png',
				],
				tabList: [{
					name: '畅销',
				}, {
					name: '新书',
				}, {
					name: '精选'
				}]
			}
		},
		onLoad() {
			this.getHomeData()
			this.$u.vuex('vuex_user', '新的值')
			console.log(this.vuex_user);
		},
		methods: {
			// 标签卡切换
			changeTab(i) {
				console.log(i);
			},
			// 请求
			async getHomeData() {
				const res = await getHome()
				console.log(res);
			}
		},
	}
</script>

<style lang="scss" scoped>
	// row不自动换行
	/deep/.u-row {
		flex-wrap: wrap;
	}
</style>
